import { Component, OnInit } from '@angular/core';
import { AlertController, ModalController } from '@ionic/angular';
import { MessageService } from 'primeng/api';
import { ItemDetailComponent } from 'src/app/components/explorar/item-detail/item-detail.component';
import { GenericHallDTO } from 'src/app/models/hall/generic-hall.dto';
import { ItemService } from 'src/app/services/criacoes/item/item.service';
import { DetalharService } from 'src/app/services/hall/detalhar/detalhar.service';
import { ItemPageService } from 'src/app/services/page/item/item-page.service';
import { StorageService } from 'src/app/services/storage-service.service';

@Component({
  selector: 'app-explore-item-defesa',
  templateUrl: './explore-item-defesa.page.html',
  styleUrls: ['./explore-item-defesa.page.scss'],
})
export class ExploreItemDefesaPage implements OnInit {

  private loading = false;
  private abaAtual = 'news';

  private projecoes: GenericHallDTO[] = [];
  private pageSize = 0;

  private linhasPorPagina = 20;

  private descricao = '';
  private orderBy = 'dataCriacao';
  private direction = 'DESC';
  private flagSearched = false;


  constructor(
    private auth: StorageService,
    private itemPage: ItemPageService,
    private modal: ModalController,
    private detalharService: DetalharService,
    private toast: MessageService,
    private alertContr: AlertController,
    private itemService: ItemService,
  ) {
    this.consultar('0');
  }

  ngOnInit() {
    this.detalharService.setTipoItem(1);
  }

  isLogedIn() {
    return this.auth.isLogedIn();
  }

  mudaAba(event) {
    //console.log(this.pageSize);
    const { value } = event.detail;
    if (value === 'search')
      this.flagSearched = false;
    else
      this.consultar('0');

  }

  consultar(page: string) {
    this.loading = true;
    this.itemPage.findPageByNewerDefesa(page, this.linhasPorPagina.toString())
      .subscribe(res => {
        const { content, totalElements } = res;
        this.projecoes = content;
        this.pageSize = totalElements;
        this.loading = false;
      },
        error => {
          console.log(error);
          this.loading = false;
        })
  }

  pesquisar(page: string) {
    this.loading = true;
    this.itemPage.findPageBySearchDefesa(page, this.descricao, this.orderBy, this.direction, this.linhasPorPagina.toString())
      .subscribe(res => {
        const { content, totalElements } = res;
        this.projecoes = content;
        this.pageSize = totalElements;
        this.loading = false;
        this.flagSearched = true;
      },
        error => {
          if (error.status == 404)
            this.handle404(error);
          else
            console.log(error);

          this.loading = false;
        })
  }


  detalhar(id) {
    this.detalharService.setId(id);
    this.showModal();
  }

  paginar(evt) {
    const { page } = evt;
    console.log(page);
    this.consultar(page.toString());
  }

  async showModal() {
    const modal = await this.modal.create({
      component: ItemDetailComponent,
      backdropDismiss: false,
      cssClass: 'my-custom-modal-css',
      id: 'npc-detail'
    });

    return await modal.present();
  }

  handle404(e) {
    this.flagSearched = false;
    this.callToast('warn', 'Famoso 404', 'Infelizmente não encontramos nada com essa palavra')
  }

  callToast(severity, summary, detail, life = 3000) {
    this.toast.clear();
    this.toast.add({
      severity,
      summary,
      detail,
      life
    });
  }

  async showDetail(projecao: GenericHallDTO) {
    const { id, apelido, idCriador, dataCriacao } = projecao;
    const localDate = new Date(dataCriacao);
    let data;

    if (!dataCriacao)
      data = "";
    else
      data = `em ${localDate.toLocaleDateString()} às ${localDate.toLocaleTimeString()}`;

    const alert = await this.alertContr.create({
      header: "Detalhes",
      message: `Criado por ${apelido} (${idCriador}) ${data}`,
      backdropDismiss: false,
      buttons: [
        {
          text: 'Copiar conteúdo',
          handler: () => { this.copiar(id) }
        },
        {
          text: 'Fechar',
        }
      ]
    });

    await alert.present();
  }

  copiar(id) {
    if (this.auth.isLogedIn()) {
      this.itemService.doCopy(id, '1')
        .subscribe(res => {
          console.log(res);
          this.callToast('success', 'Copiado com sucesso!', 'Você pode encontrá-lo no menu de criações.', 10000);
        },
          error => {
            console.log(error);
            this.callToast('error', 'Falha!', 'Não foi possível realizar cópia do conteúdo.', 10000);
          });

    } else {
      this.callToast('warn', 'Login necessário!', 'Você precisa estar logado para realizar esta ação.', 10000);
    }
  }

}
